
.site-nav {
  position: fixed;
  /*border-bottom: 1px solid #ddd;*/
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 999;
}
.table-of-content-toggle {
  display: block;
  height: 40px;
  padding: 0 15px;
  font-size: 14px;
  color: #fff;
  line-height: 40px;
  text-decoration: none;
}

/*uikit override*/
.uk-navbar {
  background: #e74c3c;
  color: #fff;
}
.uk-navbar-nav > li > a {
  color: #fff;
}
.uk-navbar-nav>li.uk-open>a, .uk-navbar-nav>li:hover>a, .uk-navbar-nav>li>a:focus {
  background-color: #ea6153;
  color: #fff;
}
.uk-navbar-nav>li.uk-active>a {
  background-color: #ea6153;
  color: #fff;
}
.uk-navbar-toggle {
  color: #fff;
}
.uk-navbar-nav>li>a {
  font-family: inherit;
}



